import { createRouter, createWebHistory } from 'vue-router'
import CustomerPage from '../views/dashbaord/CustomerPage.vue'
import ProductPage from '../views/dashbaord/ProductPage.vue'
import HomeView from '../views/HomeView.vue';
import NotFound from '../views/NotFound.vue';
import DashboardView from '../views/DashboardView.vue'

const routes = [
  {
    path: '/dashboard',
    name: 'dashboard',
    component: DashboardView,
    children: [
      { path: 'products' ,name:"products" , component: ProductPage },
      { path: 'customers',name:"customers", component: CustomerPage }
    ]
  },
  {
    path: '/test',
    name: 'test',
    component: HomeView
  },
  {
    path: '/:catchAll(.*)',
    component: NotFound,
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
